body {
  background-color: #c73a66;
}
header,
footer {
  background-color: #f77d6f !important;
}
textarea {
  resize: none !important;
  border: none !important;
}
.messagelist {
  padding: 0px 5px;
  height: 400px;
  overflow-y: scroll;
  background: url("https://pm1.narvii.com/7386/9ed808c8235082e38e0c2dd3698feb774463ee0fr1-719-404v2_uhq.jpg");

}
.messageform {
  border-top: 1px solid #DDD;
}
.text {
  border-radius: 5px;
  padding: 5px;
  width: 300px;
  margin-bottom: 5px;
  word-break: break-all;
  white-space: pre-line;
}
.primary .text {
  background-color: #d3c7ff;
  border: 5px solid #DDDDDD;
}
.secondary .text {
  background-color: #ebe6ff;
  border: 5px solid #DDDDDD;
}
aside {
  border-left: 1px solid #DDD;
  background: url("https://64.media.tumblr.com/d96e0a1abce18b8a6028a0935af5c1b4/tumblr_p5m8zbSbqz1x7362vo6_500.png");
}
.emoji-wrapper button {
  position: relative;
}
.popover {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  left: 0%;
  bottom: 18%;
  margin-top: 10px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  min-width: 200px;
  max-height: 150px;
  overflow-y: scroll;
}
.emoji-wrapper:hover .popover {
  z-index: 10;
  opacity: 1;
  visibility: visible;
}



